import React, { useEffect, useState } from 'react';
import { ThemeProvider } from 'styled-components';
import redWine from 'react95/dist/themes/redWine';
import AppBarComponent from './AppBarComponent';
import WindowComponentProject from './WindowComponentProject';
import './style.css';
import cat from './cat.png';
import Kesasim from './ProjectHeaders/Kesasim.png';
import Journey from './ProjectHeaders/Journey.png';
import ChickenWare from './ProjectHeaders/ChickenWare.png';
import PolygxnUtils from './ProjectHeaders/PolygxnUtils.png';

const PortfolioPage = () => {
  const [isMobile, setIsMobile] = useState(false);
  const [mainScale, setMainScale] = useState(1);

  useEffect(() => {
    const checkMobile = () => {
      const isNarrowScreen = window.matchMedia('(max-width: 1000px)').matches;
      const isMobileDevice = /Android|webOS|iPhone|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
      setIsMobile(isNarrowScreen || isMobileDevice);
    };

    const calculateScale = () => {
      const baseWidth = 1900;
      const baseHeight = 1000;
      const currentWidthScale = window.innerWidth / baseWidth;
      const currentHeightScale = window.innerHeight / baseHeight;
      const finalScale = 2*Math.min(currentWidthScale, currentHeightScale);
      setMainScale(finalScale);
    };

    checkMobile();
    calculateScale();
    window.addEventListener('resize', checkMobile);
    window.addEventListener('resize', calculateScale);

    return () => {
      window.removeEventListener('resize', checkMobile);
    };
  }, []);
  return (
    <ThemeProvider theme={redWine}>
      <span className="ms-sans-serif">
        <div style={{ paddingTop: `${130*mainScale}px`}}>
            <WindowComponentProject
                title = "Kesäsimulaattori"
                content = "An ages old project, made in Unity 2020.1.1f1, HDRP utilized and C# typed all the way through. Contains light shaderwork in the mix, like compute gerstner wave shaders."
                contentSideRight = "A certified Finnish summer experience - down to the finite details. A passion project for a while - before the inevitablility of it all pushed towards new winds. Release on GitHub is outdated."
                width = {600}
                height = {420}
                posX={14}
                posY={8}
                img = {Kesasim}
                imgwidth = {320}
                imgHeight={180}
                githubLink={"https://github.com/urkkiz225/kesasimulaattori"}
              WindowComponentProject/>
              <WindowComponentProject
                title = "Journey"
                contentSideLeft = "An old horror game-esque high-fidelity audiovisual demo - key points are sound and environment design."
                content = "Made utilizing HDRP and the Unity game engine."
                width = {525}
                height = {380}
                posX={50}
                posY={81}
                img = {Journey}
                imgwidth = {320}
                imgHeight={180}
                githubLink={"https://github.com/urkkiz225/Journey"}
                alternativeButtonText={"Project Github"}
              WindowComponentProject/>
              <WindowComponentProject
                title = "ChickenWare"
                content = "Written in Java, heavily utilizing the Minecraft source code. Programmed in 2020-2021."
                contentSideRight = "A Minecraft base game utility client modification - allows for gamescapes otherwise entirely impossible."
                width = {480}
                height = {410}
                posX={15}
                posY={134}
                img = {ChickenWare}
                imgwidth = {250}
                imgHeight = {200}
                githubLink={"https://github.com/urkkiz225/ChickenWare"}
              WindowComponentProject/>
              <WindowComponentProject
                title = "PolygxnUtils"
                content = "A command-documented program that allows for the creation, manipulation and transformation of polygons - along with a lot of cool stuff slapped onto it."
                contentSideLeft = "Written in Java for a computer scienec course - comes with a custom command line interface, and a graphicla user interface utulizing Java AWT."
                width = {600}
                height = {390}
                posX={40}
                posY={205}
                img = {PolygxnUtils}
                imgwidth = {320}
                imgHeight = {180}
                githubLink={"https://github.com/urkkiz225/PointPolygonUtils"}
              WindowComponentProject/>
            </div>
            {!isMobile ? (
              <div>
                <img src={cat} style={{ position: 'absolute', bottom: `${-1230 * mainScale}px` }} alt='cat' className="cat" />
                <h1 style = {{position:'relative', bottom:`${-1600*Math.sqrt(mainScale)}px`, minHeight: '10vh', color: 'white', transform: `scale(${1.1})`, textWrap:'pretty', textAlign: 'center', paddingLeft: '50px', paddingRight: '50px'}}>This is my portfolio page is for my code projects only! For other dabblings and witchcrafts - like art and music, refer to <a href="/gallery" style={{ color: 'red' }}>Gallery</a>.</h1>
              </div>
            ):(
              <div>
                <h1 style = {{position:'relative', bottom:`${-2000*Math.sqrt(mainScale)}px`, minHeight: '15vh', color: 'white', transform: `scale(${1.1})`, textWrap:'pretty', textAlign: 'center', paddingLeft: `${50*Math.sqrt(mainScale)}px`, paddingRight:`${50*Math.sqrt(mainScale)}px`}}>This is my portfolio page is for my code projects only! For other dabblings and witchcrafts - like art and music, refer to <a href="/gallery" style={{ color: 'red' }}>Gallery</a>.</h1>
              </div>
            )}
          <AppBarComponent />
        </span>
    </ThemeProvider>
  );
};

export default PortfolioPage;